interface IcheckReturnFormParams {
  name: string;
  country: string;
  agreement: boolean;
  city: string;
  zip: string;
  adress: string;
  image: File | null;
  reasonReturn: string;
}

export const checkReturnForm = (params: IcheckReturnFormParams): string[] => {
  const { name, country, agreement, city, zip, adress, image, reasonReturn } = params;
  const errorMessage = [] as string[];

  if (name.split(' ').length < 3) errorMessage.push('*   ');
  if (country === '') errorMessage.push('*  ');
  if (!agreement) errorMessage.push('* ');
  if (city === '') errorMessage.push('* ');
  if (zip === '') errorMessage.push('*  ');
  if (adress === '') errorMessage.push('* ');
  if (!image) errorMessage.push('*    ');
  if (reasonReturn === '') errorMessage.push('*  ');
  return errorMessage;
};
